import { Link } from "react-router-dom";

export function ProjectTile(props) {
    return (
        <Link to={
            {
                pathname: "/" + props.id,
            }
            }
             >
            <div className={`flex items-center justify-start my-6 bg-${props.color} hover:scale-105 transform-gpu ease-in-out duration-200 transition`}>
                <img src={props.image} alt="" srcset="" className="w-2/5" />
                <div className="mb-6 sm:mb-0 mx-4 sm:mx-0 w-3/5 sm:mt-0 mt-4">
                    <p className="sm:text-4xl text-xl font-bold sm:mb-4">{props.title}</p>
                    <p className="text-sm">{props.subtitle}</p>
                </div>
            </div>
        </Link>
    );
}
